import { newE2EPage } from '@stencil/core/testing';

describe('Nylas Calendar Picker E2E Tests', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<nylas-calendar-picker></nylas-calendar-picker>');

    const element = await page.find('nylas-calendar-picker');
    expect(element).toHaveClass('hydrated');
  });

  it('should reflect the name property as an attribute', async () => {
    const page = await newE2EPage();
    await page.setContent('<nylas-calendar-picker name="my-calendar"></nylas-calendar-picker>');

    // After page load, check if the component has the expected 'name' attribute
    const element = await page.find('nylas-calendar-picker');
    expect(await element.getAttribute('name')).toEqual('my-calendar');
  });
});
